html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

:root {
  --pale-blue: hsl(225, 100%, 94%);
  --bright-blue: hsl(245, 75%, 52%);
  --very-pale-blue: hsl(225, 100%, 98%);
  --desaturated-blue: hsl(224, 23%, 55%);
  --dark-blue: hsl(223, 47%, 23%);
}

body {
  font-family: "Red Hat Display", sans-serif;
}

.component {
  width: 28.125rem;
  height: 43.5625rem;
  border-radius: 1.25rem;
  overflow: hidden;
  background-color: #fff;
}
@media (max-width: 39.9375em) {
  .component {
    margin: 1.25rem;
  }
}
.component__layout {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 3rem;
}
.component__title {
  font-weight: 700;
  margin-bottom: 1.875rem;
}
.component__text {
  text-align: center;
  margin-bottom: 1.5rem;
}
.component__container {
  display: flex;
  gap: 5.375rem;
  align-items: center;
  background-color: var(--very-pale-blue);
  padding: 1.5625rem;
  border-radius: 0.625rem;
  margin-bottom: 2rem;
}
.component__ann1 {
  display: flex;
}
.component__ann1 img {
  margin-right: 0.9375rem;
}
.component__plan {
  font-weight: 700;
  margin-bottom: 0.75rem;
}
.component__change {
  font-weight: 700;
  color: var(--bright-blue);
  text-decoration: underline;
  cursor: pointer;
}
.component__change:hover {
  color: var(--desaturated-blue);
}
.component__buttons {
  display: flex;
  flex-direction: column;
  font-weight: 700;
}
.component__proceed {
  font-weight: 700;
  font-size: 0.9375rem;
  border-radius: 0.625rem;
  border: none;
  padding: 1.25rem 6.5625rem;
  color: #fff;
  background-color: var(--bright-blue);
  margin-bottom: 1.5625rem;
  cursor: pointer;
}
.component__proceed:hover {
  background-color: var(--desaturated-blue);
}
.component__cancel {
  text-align: center;
  color: var(--desaturated-blue);
}
.component__cancel:hover {
  color: #000;
  cursor: pointer;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: var(--very-pale-blue);
  background-image: url(/app/images/pattern-background-desktop.svg);
  background-repeat: no-repeat;
  background-size: 100%;
}